<template>
    <div class="fed-roller-title-content">
        <a class="fed-roller-cancel" title="cancelTxt"  @click="btnClick()" >{{cancelTxt}}</a>
        <h3>{{title}}</h3>
        <a class="fed-roller-ok" title="okTxt" @click="btnClick(true)" >{{okTxt}}</a>
    </div>

</template>
<script>
const empty = ()=>{}
export default {
  name: 'RollerTopView',
  props:["title","cancelTxt","okTxt"],
  components: {
  },
  data (){
    return {
    }
  },
  beforeCreate (){
  },
  destroyed (){
  },
  methods :{
	
    btnClick(isok) {
      this.$emit('callback',!!isok)
    }
  }
}
</script>




